import './App.css';
import {useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
function App() {


const [tehtavat,setTehtavat]=useState([

                                        {
                                          nimi:"Siivoa",
                                          suoritettu:false
                                        },
                                        {
                                          nimi:"Käy kaupassa",
                                          suoritettu:true
                                        },
                                        {
                                          nimi:"Koira ulos",
                                          suoritettu:false
                                        }

                                        ]);

const lisaaTehtava=(uusiTehtava)=>{ 

  let apuTehtava = {
                    nimi:uusiTehtava,
                    suoritettu:false
                    }

  setTehtavat([...tehtavat,apuTehtava]);

}

const merkitseSuoritetuksi=(indeksi)=>{

  //let apu = ...tehtavat;

tehtavat[indeksi].suoritettu= !tehtavat[indeksi].suoritettu;

setTehtavat([...tehtavat]);



}

  return (
    <div className="container">
      <h2>Demo2: React perusteita osa 2</h2>

      <h2>Tehtävälista</h2>

      <input  className="form-control mb-4"
              placeholder="Lisää työ ja paina Enter..."
              onKeyPress={(e)=>{
                                if(e.key==="Enter"){

                                  lisaaTehtava(e.target.value);
                                  e.target.value=null;

                                }
                                
                          }}
              />

      

      <ul className="list-group">

      { tehtavat.map((tehtava,idx)=>{

       
        
        return (<li 
                className="list-group-item"
                key={idx}
                onClick={()=>{
                              merkitseSuoritetuksi(idx);
                              
                            }}
                >

                {(tehtava.suoritettu)
                  ?<del>{tehtava.nimi}</del>
                  :tehtava.nimi
                  }</li>)
      })  }
      

      </ul>

<br/>
<p><small>Klikkaa tehtävän nimeä merkataksesi sen suoritetuksi.</small></p>

    </div>
  );
}

export default App;
